import React from 'react'
import { Link } from 'gatsby'
import { graphql } from 'gatsby'
import Layout from '../components/layout'
import BackButton from '../components/back-button';

const WebIndex = ({ data }) => (
  <div id="web-all">
    <Layout>
      <div class="container">
        <div class="row">
          <div class="col-12">
            <BackButton />
            <h1>Web Projects</h1>
            <div className="flexgrid">
              {data.allMarkdownRemark.edges.map(post => (
                <Link className="card-wrapper" key={post.node.id} to={post.node.frontmatter.path}>
                  <div className="card card--web">
                    <h3>{post.node.frontmatter.title}</h3>
                    <p>{post.node.frontmatter.desc}</p>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        </div>
      </div>
    </Layout>
  </div>
)

export const pageQuery = graphql`
  query WebIndexQuery {
    allMarkdownRemark(
      filter: { frontmatter: { template: { eq: "web" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            path
            title
            date
            author
            template
            desc
          }
        }
      }
    }
  }
`

export default WebIndex;
